}

img {
  max-width: 100%;
  height: auto;
  border: 0;
}

img[width],
img[height] {
  max-width: none;
}

svg:not(:root) {
  overflow: hidden;
}

a {
  background: transparent;
  color: #be4bdb;
  text-decoration: none;
}

a:visited {
