  position: relative;
  vertical-align: baseline;
}

sup {
  top: -0.5rem;
}

sub {
  bottom: -0.5rem;
}

hr {
  height: 0;
}

kbd {
  background-color: whitesmoke;
  border: 1px solid #b5b5b5;
  padding: 0.25rem;
  border-radius: 3px;
}

samp,
code {
  background-color: whitesmoke;
  color: #fa5252;
  display: block;
  padding: 1rem;
  font-family: "Inconsolata", "Consolas", "Monaco", monospace;
}

samp,
pre {
  overflow: auto;
  margin: 0;
}

.content h1, .content h2, .content h3, .content h4, .content h5, .content h6 {
  font-weight: 300;
  line-height: 1.125;
  margin-bottom: 2rem;
}

.content h1:not(:first-child), .content h2:not(:first-child), .content h3:not(:first-child), .content h4:not(:first-child), .content h5:not(:first-child), .content h6:not(:first-child) {
  margin-top: 3rem;
}

.content h1 {
  font-size: 28px;
  font-weight: 700;
}

.content h2 {
  font-size: 24px;
}

.content h3 {
  font-size: 18px;
}

.content h4 {
  font-size: 14px;
}

.content h5, .content h6 {
  font-size: 11px;
}

.content h6 {
  text-transform: uppercase;
  letter-spacing: 1px;
}

.content ul, .content ol {
  margin-left: 2rem;
  margin-right: 2rem;
  margin-top: 1rem;
}

.content ol {
  list-style: decimal outside;
}

.content ol ol {
  list-style: upper-roman outside;
}

.content ol ol ol {
  list-style: lower-roman outside;
}

.content ul {
  list-style: disc outside;
}

.content ul ul {
  list-style-type: circle;
  margin-top: 0.5rem;
}

.content ul ul ul {
  list-style-type: square;
}

.content li + li {
  margin-top: 0.25rem;
}

.content blockquote {
  background-color: whitesmoke;
  border-left: 5px solid #b5b5b5;
  padding: 1.5rem;
  font-style: italic;
  text-indent: -0.41rem;
  quotes: '"' '"';
}

.content blockquote p:before {
  content: '"';
  content: open-quote;
}

.content blockquote p:after {
  content: "";
  content: no-close-quote;
}

.content blockquote p:last-of-type {
  margin-bottom: 0;
}

.content blockquote p:last-of-type:after {
  content: '"';
  content: close-quote;
}

.content blockquote q:before {
  content: "\2018";
  content: open-quote;
}

.content blockquote q:after {
  content: "\2019";
  content: close-quote;
}

.content blockquote:not(:last-child),
.content p:not(:last-child),
.content ol:not(:last-child),
.content ul:not(:last-child),
.content code:not(:last-child) {
  margin-bottom: 1rem;
}

.section {
  border-bottom: 1px solid #b5b5b5;
}

.section:not(:first-child) {
  border-bottom: none;
}

.section.has-padding {
  padding: 2rem 1rem;
}

.container {
  max-width: 1180px;
  margin: 0 auto;
}

.columns {
  margin-left: -10px;
  margin-right: -10px;
  margin-top: -10px;
}

@media (min-width: 48em) {
  .columns {
    display: flex;
  }
