.button {
  -moz-appearance: none;
  -webkit-appearance: none;
  align-items: center;
  background-color: white;
  border: 1px solid #b5b5b5;
  border-radius: 3px;
  display: inline-flex;
  font-size: 14px;
  height: 32px;
  justify-content: flex-start;
  line-height: 24px;
  padding-left: 8px;
  padding-right: 8px;
  position: relative;
  vertical-align: top;
  -webkit-touch-callout: none;
  user-select: none;
  cursor: pointer;
  justify-content: center;
  padding-left: 10px;
  padding-right: 10px;
  text-align: center;
