.is--hidden {
  display: none;
  visibility: hidden;
}

.is--visiable {
  display: block;
  visibility: visible;
}

body:before {
  display: block;
  content: 'mobile-first: 0-33em';
  font-family: monospace;
  text-align: center;
  background-color: #40c057;
  color: #fff;
}

