  body:before {
    content: 'baby: 34em';
    background-color: #228ae6;
  }
}

@media (min-width: 48em) {
  body:before {
    content: 'toddler: 48em';
    background-color: #be4bdb;
  }
}

@media (min-width: 62em) {
  body:before {
    content: 'teenager: 62em';
    background-color: #82c91e;
